import React from 'react';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import { withStyles } from '@material-ui/core/styles';

import propTypes, { defaultProps } from './propTypes';
import styles from './styles';

const CompactListItem = ({ classes, icon, text }) => (
  <ListItem disableGutters className={classes.root}>
    <ListItemIcon className={classes.itemIcon}>
      {icon()}
    </ListItemIcon>
    <ListItemText className={classes.itemText} primary={text} primaryTypographyProps={{variant: 'body1'}} />
  </ListItem>
)

CompactListItem.propTypes = propTypes;
CompactListItem.defaultProps = defaultProps;

export default withStyles(styles)(CompactListItem);
