import React from 'react';
import Grid from '@material-ui/core/Grid';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';

import EmailListItem from './EmailListItem'
import PhoneListItem from './PhoneListItem'
import PlaceListItem from './PlaceListItem'
import mapSrc from './map.png'

import propTypes, { defaultProps } from './propTypes';
import styles from './styles';

const places = [
  'Bayswater',
  'Boronia',
  'Chirnside Park',
  'Croydon',
  'Ferntree Gully',
  'Kilsyth',
  'Lilydale',
  'Mooroolbark',
  'Montrose',
  'Ringwood',
  'Wantirna',
];

const ContactSection = ({ classes }) => (
  <div className={classes.root}>
    <Typography variant="h2" className={classes.heading}>
      Contact
    </Typography>
    <Grid container direction="row" justify="center" spacing={16}>
      <Grid item xs={12} sm={6}>
        <div className={classes.contactContainer}>
          <Typography>
            Contact Andrew for a free Quote:
          </Typography>
          <List>
            <PhoneListItem text="0408 242 015" />
            <EmailListItem text="andrew@scrr.com.au" />
          </List>
        </div>
        <div className={classes.areaContainer}>
          <Typography>
            We service Melbourne's eastern suburbs including:
          </Typography>
          <List>
            {places.map(place => <PlaceListItem text={place} />)}
          </List>
        </div>
      </Grid >
      <Grid item xs={12} sm={6}>
        <img src={mapSrc} alt="Map" className={classes.mapImage}/>
      </Grid>
    </Grid>
  </div>
);

ContactSection.propTypes = propTypes;
ContactSection.defaultProps = defaultProps;

export default withStyles(styles)(ContactSection);
